<template>
  <view class="supplier-container">
    <!-- Â§¥ÈÉ®ÂäüËÉΩÂå∫ -->
    <view class="header">
      <!-- Êâ´Á†ÅÊåâÈíÆ -->
      <view class="scan-btn" @tap="handleScan">
        <text class="scan-icon">üì∑</text>
      </view>
      
      <!-- ‰ªäÊó•ÁªüËÆ° -->
      <view class="stats-card">
        <view class="stats-item">
          <text class="stats-label">‰ªäÊó•Êèê‰∫§</text>
          <text class="stats-value">{{todayStats.count}}Âçï</text>
        </view>
        <view class="stats-item">
          <text class="stats-label">ÊÄªÈáëÈ¢ù</text>
          <text class="stats-value">Ôø•{{formatPrice(todayStats.amount)}}</text>
        </view>
      </view>
    </view>

    <!-- ‰∏ª‰ΩìË°®ÂçïÂå∫ -->
    <view class="form-section">
      <!-- ‰∫ßÂìÅÂêçÁß∞ -->
      <view class="form-item">
        <text class="label"><text class="required">*</text>‰∫ßÂìÅÂêçÁß∞</text>
        <view class="input-wrap">
          <input
            class="input"
            v-model="formData.productName"
            placeholder="ËØ∑ËæìÂÖ•ÊàñÈÄâÊã©‰∫ßÂìÅÂêçÁß∞"
            @input="onProductNameInput"
            @focus="showHistory = true"
            @blur="onProductNameBlur"
          />
          <!-- ÂéÜÂè≤ËÆ∞ÂΩï‰∏ãÊãâÊ°Ü -->
          <view class="history-dropdown" v-if="showHistory && filteredHistory.length">
            <view 
              class="history-item"
              v-for="(item, index) in filteredHistory"
              :key="index"
              @tap="selectHistory(item)"
            >
              <text class="item-name">{{item.name}}</text>
              <text class="item-price">Ôø•{{formatPrice(item.price)}}</text>
            </view>
          </view>
        </view>
      </view>

      <!-- Êï∞Èáè -->
      <view class="form-item">
        <text class="label"><text class="required">*</text>Êï∞Èáè</text>
        <view class="stepper">
          <text class="step-btn" @tap="updateQuantity(-1)">-</text>
          <input
            class="step-input"
            type="number"
            v-model="formData.quantity"
            @input="validateQuantity"
          />
          <text class="step-btn" @tap="updateQuantity(1)">+</text>
        </view>
      </view>

      <!-- Âçï‰ª∑ -->
      <view class="form-item">
        <text class="label"><text class="required">*</text>Âçï‰ª∑</text>
        <view class="price-input-wrap">
          <input
            class="input"
            type="digit"
            v-model="formData.price"
            :class="{'price-warning': isPriceWarning}"
            @input="validatePrice"
          />
          <view class="price-tips" v-if="priceInfo.show">
            <text class="tip-item">ÂéÜÂè≤Âùá‰ª∑ÔºöÔø•{{formatPrice(priceInfo.avgPrice)}}</text>
            <text class="tip-item">‰∏äÊ¨°‰ª∑Ê†ºÔºöÔø•{{formatPrice(priceInfo.lastPrice)}}</text>
          </view>
        </view>
      </view>

      <!-- ÊÄª‰ª∑Â±ïÁ§∫ -->
      <view class="total-section">
        <text class="total-label">ÊÄª‰ª∑</text>
        <text class="total-value">Ôø•{{formatPrice(totalAmount)}}</text>
      </view>
    </view>

    <!-- Â∫ïÈÉ®ÂØºËà™ -->
    <view class="footer">
      <button 
        class="submit-btn"
        :disabled="!canSubmit"
        @tap="handleSubmit"
      >Êèê‰∫§ËÆ¢Âçï</button>
      <text class="history-link" @tap="toHistory">ÂéÜÂè≤ËÆ∞ÂΩï</text>
    </view>

    <!-- Á¶ªÁ∫øÊèêÁ§∫ -->
    <view class="offline-tip" v-if="isOffline">
      <text>ÂΩìÂâçÂ§Ñ‰∫éÁ¶ªÁ∫øÁä∂ÊÄÅÔºåÊï∞ÊçÆÂ∞ÜÂú®ÁΩëÁªúÊÅ¢Â§çÂêéÊèê‰∫§</text>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      // Ë°®ÂçïÊï∞ÊçÆ
      formData: {
        productName: '',
        quantity: 1,
        price: ''
      },
      // ‰ªäÊó•ÁªüËÆ°
      todayStats: {
        count: 0,
        amount: 0
      },
      // ÂéÜÂè≤ËÆ∞ÂΩï
      historyList: [],
      showHistory: false,
      // ‰ª∑Ê†º‰ø°ÊÅØ
      priceInfo: {
        show: false,
        avgPrice: 0,
        lastPrice: 0,
        minPrice: 0 // ÊàêÊú¨‰ª∑
      },
      // ÁΩëÁªúÁä∂ÊÄÅ
      isOffline: false
    }
  },
  computed: {
    // ËøáÊª§ÂêéÁöÑÂéÜÂè≤ËÆ∞ÂΩï
    filteredHistory() {
      if(!this.formData.productName) return []
      return this.historyList
        .filter(item => item.name.includes(this.formData.productName))
        .slice(0, 5)
    },
    // ÊÄª‰ª∑
    totalAmount() {
      return this.formData.quantity * (Number(this.formData.price) || 0)
    },
    // ‰ª∑Ê†ºÊòØÂê¶Ë∂ÖÂá∫È¢ÑË≠¶
    isPriceWarning() {
      if(!this.formData.price) return false
      return Math.abs(this.formData.price - this.priceInfo.avgPrice) / this.priceInfo.avgPrice > 0.15
    },
    // ÊòØÂê¶ÂèØÊèê‰∫§
    canSubmit() {
      return this.formData.productName.length >= 2 && 
        this.formData.quantity >= 1 &&
        this.formData.price >= this.priceInfo.minPrice
    }
  },
  onLoad() {
    // ÂàùÂßãÂåñÊï∞ÊçÆ
    this.initData()
    // ÁõëÂê¨ÁΩëÁªúÁä∂ÊÄÅ
    uni.onNetworkStatusChange(res => {
      this.isOffline = !res.isConnected
      if(res.isConnected) {
        this.syncOfflineData()
      }
    })
    // ÊÅ¢Â§çËçâÁ®ø
    this.restoreDraft()
  },
  methods: {
    // ÂàùÂßãÂåñÊï∞ÊçÆ
    async initData() {
      try {
        // TODO: Ë∞ÉÁî®Êé•Âè£Ëé∑ÂèñÊï∞ÊçÆ
        this.todayStats = {
          count: 5,
          amount: 12345.67
        }
        this.historyList = [
          { name: 'Á§∫‰æã‰∫ßÂìÅ1', price: 100 },
          { name: 'Á§∫‰æã‰∫ßÂìÅ2', price: 200 }
        ]
        this.priceInfo = {
          show: false,
          avgPrice: 150,
          lastPrice: 160,
          minPrice: 80
        }
      } catch(e) {
        console.error(e)
        uni.showToast({
          title: 'Êï∞ÊçÆÂä†ËΩΩÂ§±Ë¥•',
          icon: 'none'
        })
      }
    },
    
    // Êâ´Á†ÅÂ§ÑÁêÜ
    handleScan() {
      uni.scanCode({
        success: (res) => {
          // TODO: Â§ÑÁêÜÊâ´Á†ÅÁªìÊûú
          console.log('Êâ´Á†ÅÁªìÊûúÔºö', res)
        }
      })
    },
    
    // ‰∫ßÂìÅÂêçÁß∞ËæìÂÖ•
    onProductNameInput() {
      this.showHistory = true
      this.saveDraft()
    },
    
    // ‰∫ßÂìÅÂêçÁß∞Â§±ÁÑ¶
    onProductNameBlur() {
      setTimeout(() => {
        this.showHistory = false
      }, 200)
    },
    
    // ÈÄâÊã©ÂéÜÂè≤ËÆ∞ÂΩï
    selectHistory(item) {
      this.formData.productName = item.name
      this.formData.price = item.price
      this.showHistory = false
      this.priceInfo.show = true
      this.saveDraft()
    },
    
    // Êõ¥Êñ∞Êï∞Èáè
    updateQuantity(delta) {
      const newValue = Number(this.formData.quantity) + delta
      if(newValue >= 1 && newValue <= 9999) {
        this.formData.quantity = newValue
        this.saveDraft()
      }
    },
    
    // È™åËØÅÊï∞Èáè
    validateQuantity(e) {
      const value = e.detail.value
      if(value < 1) this.formData.quantity = 1
      if(value > 9999) this.formData.quantity = 9999
      this.saveDraft()
    },
    
    // È™åËØÅ‰ª∑Ê†º
    validatePrice() {
      this.priceInfo.show = true
      this.saveDraft()
    },
    
    // Ê†ºÂºèÂåñ‰ª∑Ê†º
    formatPrice(price) {
      return Number(price).toLocaleString('zh', {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2
      })
    },
    
    // Êèê‰∫§ËÆ¢Âçï
    async handleSubmit() {
      if(!this.canSubmit) return
      
      // ‰ª∑Ê†ºÈ¢ÑË≠¶Á°ÆËÆ§
      if(this.isPriceWarning) {
        const confirmed = await new Promise(resolve => {
          uni.showModal({
            title: '‰ª∑Ê†ºÂºÇÂ∏∏ÊèêÈÜí',
            content: 'ÂΩìÂâçÂçï‰ª∑‰∏éÂéÜÂè≤Âùá‰ª∑Áõ∏Â∑ÆË∂ÖËøá15%ÔºåÊòØÂê¶ÁªßÁª≠Êèê‰∫§Ôºü',
            success: res => resolve(res.confirm)
          })
        })
        if(!confirmed) return
      }
      
      try {
        uni.showLoading({title: 'Êèê‰∫§‰∏≠'})
        // TODO: Ë∞ÉÁî®Êèê‰∫§Êé•Âè£
        await new Promise(resolve => setTimeout(resolve, 1000))
        
        // Êèê‰∫§ÊàêÂäü
        uni.hideLoading()
        uni.showToast({
          title: 'Êèê‰∫§ÊàêÂäü',
          icon: 'success'
        })
        // Ê∏ÖÁ©∫Ë°®Âçï
        this.formData = {
          productName: '',
          quantity: 1,
          price: ''
        }
        this.clearDraft()
      } catch(e) {
        uni.hideLoading()
        if(this.isOffline) {
          // Á¶ªÁ∫øÂ≠òÂÇ®
          this.saveOfflineData()
          uni.showToast({
            title: 'Â∑≤‰øùÂ≠òËá≥Á¶ªÁ∫øÊï∞ÊçÆ',
            icon: 'none'
          })
        } else {
          uni.showToast({
            title: 'Êèê‰∫§Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
            icon: 'none'
          })
        }
      }
    },
    
    // ‰øùÂ≠òËçâÁ®ø
    saveDraft() {
      uni.setStorageSync('supplierDraft', this.formData)
    },
    
    // ÊÅ¢Â§çËçâÁ®ø
    restoreDraft() {
      const draft = uni.getStorageSync('supplierDraft')
      if(draft) {
        this.formData = draft
      }
    },
    
    // Ê∏ÖÈô§ËçâÁ®ø
    clearDraft() {
      uni.removeStorageSync('supplierDraft')
    },
    
    // ‰øùÂ≠òÁ¶ªÁ∫øÊï∞ÊçÆ
    saveOfflineData() {
      const offlineData = uni.getStorageSync('offlineData') || []
      offlineData.push({
        ...this.formData,
        timestamp: Date.now()
      })
      uni.setStorageSync('offlineData', offlineData)
    },
    
    // ÂêåÊ≠•Á¶ªÁ∫øÊï∞ÊçÆ
    async syncOfflineData() {
      const offlineData = uni.getStorageSync('offlineData')
      if(!offlineData?.length) return
      
      try {
        // TODO: Ë∞ÉÁî®ÊâπÈáèÊèê‰∫§Êé•Âè£
        await new Promise(resolve => setTimeout(resolve, 1000))
        
        // ÂêåÊ≠•ÊàêÂäüÔºåÊ∏ÖÈô§Á¶ªÁ∫øÊï∞ÊçÆ
        uni.removeStorageSync('offlineData')
        uni.showToast({
          title: 'Á¶ªÁ∫øÊï∞ÊçÆÂêåÊ≠•ÊàêÂäü',
          icon: 'success'
        })
      } catch(e) {
        console.error('Á¶ªÁ∫øÊï∞ÊçÆÂêåÊ≠•Â§±Ë¥•', e)
      }
    },
    
    // Ë∑≥ËΩ¨ÂéÜÂè≤ËÆ∞ÂΩï
    toHistory() {
      uni.navigateTo({
        url: '/pages/supplier/history'
      })
    }
  }
}
</script>

<style>
.supplier-container {
  min-height: 100vh;
  background: #f8f8f8;
  padding-bottom: 120rpx;
}

/* Â§¥ÈÉ®Ê†∑Âºè */
.header {
  position: relative;
  padding: 20rpx;
}

.scan-btn {
  position: absolute;
  right: 40rpx;
  top: 40rpx;
  width: 80rpx;
  height: 80rpx;
  background: #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2rpx 10rpx rgba(0,0,0,0.1);
}

.scan-icon {
  font-size: 40rpx;
}

.stats-card {
  background: #fff;
  border-radius: 12rpx;
  padding: 30rpx;
  display: flex;
  justify-content: space-around;
}

.stats-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.stats-label {
  font-size: 24rpx;
  color: #666;
  margin-bottom: 10rpx;
}

.stats-value {
  font-size: 32rpx;
  color: #333;
  font-weight: bold;
}

/* Ë°®ÂçïÊ†∑Âºè */
.form-section {
  background: #fff;
  margin: 20rpx;
  padding: 30rpx;
  border-radius: 12rpx;
}

.form-item {
  margin-bottom: 30rpx;
}

.label {
  font-size: 28rpx;
  color: #333;
  margin-bottom: 16rpx;
  display: block;
}

.required {
  color: #ff4d4f;
  margin-right: 4rpx;
}

.input-wrap {
  position: relative;
}

.input {
  height: 80rpx;
  background: #f8f8f8;
  border-radius: 8rpx;
  padding: 0 20rpx;
  font-size: 28rpx;
}

.history-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: #fff;
  border-radius: 8rpx;
  box-shadow: 0 2rpx 10rpx rgba(0,0,0,0.1);
  z-index: 100;
}

.history-item {
  padding: 20rpx;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1rpx solid #f0f0f0;
}

.item-name {
  font-size: 28rpx;
  color: #333;
}

.item-price {
  font-size: 24rpx;
  color: #666;
}

.stepper {
  display: flex;
  align-items: center;
}

.step-btn {
  width: 60rpx;
  height: 60rpx;
  background: #f8f8f8;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36rpx;
  color: #666;
}

.step-input {
  width: 120rpx;
  height: 60rpx;
  background: #f8f8f8;
  margin: 0 20rpx;
  text-align: center;
  font-size: 28rpx;
}

.price-input-wrap {
  position: relative;
}

.price-warning {
  border: 2rpx solid #ff4d4f;
}

.price-tips {
  position: absolute;
  top: 100%;
  right: 0;
  background: #fff;
  padding: 10rpx 20rpx;
  border-radius: 8rpx;
  box-shadow: 0 2rpx 10rpx rgba(0,0,0,0.1);
}

.tip-item {
  font-size: 24rpx;
  color: #666;
  display: block;
}

.total-section {
  margin-top: 40rpx;
  padding-top: 30rpx;
  border-top: 1rpx solid #f0f0f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.total-label {
  font-size: 32rpx;
  color: #333;
  font-weight: bold;
}

.total-value {
  font-size: 40rpx;
  color: #ff4d4f;
  font-weight: bold;
}

/* Â∫ïÈÉ®ÂØºËà™ */
.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #fff;
  padding: 20rpx;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 -2rpx 10rpx rgba(0,0,0,0.05);
}

.submit-btn {
  flex: 1;
  height: 80rpx;
  line-height: 80rpx;
  background: #3cc51f;
  color: #fff;
  font-size: 32rpx;
  border-radius: 40rpx;
  margin: 0;
  margin-right: 20rpx;
}

.history-link {
  font-size: 28rpx;
  color: #666;
  padding: 20rpx;
}

/* Á¶ªÁ∫øÊèêÁ§∫ */
.offline-tip {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #fffbe6;
  padding: 10rpx;
  text-align: center;
  font-size: 24rpx;
  color: #faad14;
}
</style> 